import React from "react"
import SEO from "../components/seo"
import Logo from "../components/logo"
import Nav from "../components/navigation"
import PlayImage from "../images/play-image.jpg"
import PageInfo from "../components/pageinfo"
import { PLAY_INFO } from "../components/siteinfo"
import { LOGO_COLOR_DARK, MAIN_TEXT_COLOR, MAIN_TEXT_COLOR_DARK } from "../components/siteconfig"
import GiantHeader from "../components/giantheader"
import 'aos/dist/aos.css';
import './styles.css';

class Play extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isActive: true
    }
  }
  componentDidMount() {
    const AOS = require('aos');
    this.aos = AOS
    this.aos.init()
  }

  componentDidUpdate() {
    this.aos.refresh()
  }

  render() {
    return (
      <div className="playbg" style={{
        display: `flex`
      }}>
        <SEO title="Play - 1240 Greene St" />
        <div css={{ width: '50%' }}>
          <Logo title="1240 Greene St" color={LOGO_COLOR_DARK} />
          <div data-aos="fade-right" data-aos-delay="300" data-aos-easing="ease-in-sine" data-aos-duration="500">
            <PageInfo header={PLAY_INFO.header} decscription={PLAY_INFO.description} color={MAIN_TEXT_COLOR} mobileMarginTop="25%" />
          </div>
        </div>

        <div css={{ zIndex: `100`, marginLeft: `auto`, justifyContent: `flex-end` }}>
          <Nav isActive={this.state.isActive} />
          <div css={{
            position: 'fixed',
            bottom: '-118px',
            left: '-51%',
            transform: 'scale(.45)',
            '@media(min-width:700px)': {
              bottom: '-67px',
              transform: 'scale(1)',
              left: '51%'
            }

          }}>
            <div data-aos="fade-left" data-aos-delay="350" data-aos-duration="500">
              <GiantHeader text="Play" color={MAIN_TEXT_COLOR_DARK} opacity=".3" stroke="1px #fff" />
            </div>
          </div>
        </div>

      </div>
    )

  }

}

export default Play
